
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import {currentdriverinfo} from "@/api/driver";
import CarItem from "./CarItem";
import setting from "../setting/main";
import service from "@/uilt/request";

export default {
created(){
var dataset=this;
var list = [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"onMenuShareQQ",
"onMenuShareWeibo",
"onMenuShareQZone",
"chooseImage",
"getNetworkType",
"hideOptionMenu",
"showOptionMenu",
"hideMenuItems",
"uploadImage",
"showMenuItems",
"hideAllNonBaseMenuItem",
"showAllNonBaseMenuItem",
"closeWindow"
];
var dataset=this;
service.get(process.env.BASE_API + "apiticket?url=" + window.location.href)
.then(signature => {
dataset.$wechat.config({
debug: true,
appId: "wx6a2e25ee7abfca60",
timestamp: "1414587457",
nonceStr: "Wm3WZYTPz0wzccnW",
signature: signature,
jsApiList: list
});

});



currentdriverinfo().then(function(result){
if(result==null||result=="")return;
dataset.driverInfo=result;
dataset.userid=result.userid;
});
},
data() {
var dataset = {
titleEnum: ["主页", "我的信息"],
pageIndex: 0,
height: 0,
userid:0,
driverInfo:{},
points:{lat:0,lon:0}
};
this.clientHeight = `${document.documentElement.clientHeight}`;
dataset.height= this.clientHeight-84;
try{
this.$wechat.getLocation({
type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
success: function (res) {
dataset.points.lat = res.latitude; 
dataset.points.lon = res.longitude; 


alert("书籍"+JSON.stringify(res));
}
});
}catch(e){
alert(e)
}
return dataset;
},
methods: {
onItem(n) {
this.pageIndex = n;
}
},
computed: {},
components: {
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
CarItem,
setting

}
};



